import React from 'react';
import DetailPresenter from './DetailPresenter';
import { moviesApi, tvApi } from 'api';

export default class DetailContainer extends React.Component {
  constructor(props) {
    super(props);

    const {
      location: { pathname }
    } = props;
    this.state = {
      result: null,
      error: null,
      loading: true,
      isMovie: pathname.includes('/movie/')
    };
  }

  async componentDidMount() {
    const {
      match: {
        params: { id }
      },
      history: { push }
    } = this.props;
    const parsedId = parseInt(id);
    const { isMovie } = this.state;

    if (isNaN(parsedId)) {
      // Ïà´ÏûêÍ∞Ä ÏïÑÎãå Í∞íÏúºÎ°ú idÏóê Ï†ëÍ∑ºÌïú Í≤ΩÏö∞
      return push('/');
    }

    let result = null;
    try {
      ({ data: result } = isMovie ? await moviesApi.movieDetail(id) : await tvApi.tvDetail(id));
    } catch {
      this.setState({ error: "Can't find anything." });
    } finally {
      this.setState({ loading: false, result });
      console.log(result);
    }
  }

  render() {
    const { result, error, loading, isMovie } = this.state;
    return <DetailPresenter result={result} error={error} loading={loading} isMovie={isMovie} />;
  }
}
